*{
  font-family: Helvetica, Verdana, Arial, sans-serif;
}

body{
  padding: 60px;
}

input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}
.card-grid{
  align-content: center;
  align-items: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  min-width: 300px;
  overflow: hidden;
  
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}